section.welcome {
  background-image: url("/images/modern_bedroom.jpg");
  width: 100%;
  height: 800px;
  display: flex;
  justify-content: center;
  align-items: center;
}
section.welcome .container-welcome {
  width: 50%;
  height: 50%;
  background-color: pink;
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 20px;
}
section.welcome .container-welcome .welcome-text {
  width: 100%;
  height: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}
section.welcome .container-welcome .logo {
  position: absolute;
  display: inline;
}
section.welcome .container-welcome .logo img {
  width: 50%;
  height: auto;
  margin-left: 40%;
  margin-top: 20%;
}
section.welcome .container-welcome .empty {
  display: inline;
  width: 30%;
  height: 20%;
  background-color: red;
}
section.welcome .container-welcome .buy {
  display: inline;
  width: 30%;
  height: 20%;
  background-color: red;
  text-align: center;
}
section.welcome .container-welcome .sell {
  display: inline;
  width: 30%;
  height: 20%;
  background-color: red;
  text-align: center;
}
section.welcome .container-welcome .rent {
  display: inline;
  width: 30%;
  height: 20%;
  background-color: red;
  text-align: center;
}
section.welcome .container-welcome .contact-btn {
  position: absolute;
  top: 97%;
  left: 50%;
  border: none;
  background-color: orangered;
  cursor: pointer;
  border-radius: 20px;
  padding: 10px 15px;
}
section.welcome .container-welcome .contact-btn:hover {
  background-color: orangered;
}