.information {
  width: 100%;
  height: 500px;
  background-color: lightgrey;
  display: flex;
  justify-content: start;
}
.information .company_information {
  margin-left: 20%;
  padding: 2rem 2rem;
}
.information .company_information > p.welcome {
  text-align: center;
  font-size: large;
  font-weight: bold;
  margin-bottom: 30%;
}
.information .company_information > .others {
  display: flex;
  flex-direction: column;
}
.information .link_to_map {
  width: 50%;
  padding: 2rem 2rem;
}
.information .link_to_map .map {
  width: 100%;
  height: 100%;
  border-radius: 2rem;
}