section.contact {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 800px;
  background-color: #ff954a;
}
section.contact .info {
  position: absolute;
  list-style: none;
  top: 130%;
}
section.contact .container-submission {
  width: 35%;
  height: 40%;
  background-color: lightblue;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  border-radius: 20px;
}
section.contact .container-submission p {
  margin-top: 10px;
  font-weight: bold;
}
section.contact .container-submission .name-surname, section.contact .container-submission .number, section.contact .container-submission .email {
  align-self: flex-start;
  margin-top: 1rem;
  margin-left: 1rem;
  border: none;
  border-radius: 20px;
  padding: 10px;
}
section.contact .container-submission .message {
  border: none;
  border-radius: 20px;
  padding: 10px;
  width: 90%;
  height: 30%;
  margin-top: 10px;
  resize: none;
}
section.contact .container-submission .btm-send {
  background-color: darkorange;
  color: white;
  cursor: pointer;
  border: none;
  border-radius: 20px;
  padding: 10px 10px;
}
section.contact .container-submission .btm-send:hover {
  background: green;
  transition: all 0.3s ease-in-out;
}